@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,300,100);
@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	box-sizing: border-box;
}

body {
	background-color: #1D1D1D;
}

.container {
	width: 100%;
	max-width: 1480px;
	padding: 0 20px;
	margin: auto;
	position: relative;
	background-color: #1A1E1F;
	z-index: 50;
}
a {
	font-size: 16px;
	text-decoration: none;
}
header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding: 20px 0;
	z-index: 20;
	background-color: #1A1E1F;
}

header h1 {
	position: relative;
	height: 40px;
	margin:0;
	float: left;
	z-index: 30;
}
header h1 img{
	display: block;
	position: relative;
	top:22px;
}


.btn {
	display: inline-block;
	color: #fff;
	font-weight: bold;
	padding: 12px 20px;
	border-radius: 8px;
	background-color: #004fff;
}

.ghost-btn {
	background-color: transparent;
	border: 1px solid #004fff;
}

.text-wrap {
	width: 520px;
	height: 100vh;
	padding: 240px 20px;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1;
}

.text-wrap h2 {
	color: #fff;
	font-size: 64px;
	line-height: 1.25;
	font-weight: lighter;
	margin-bottom: 40px;
}

.text-wrap h3 {
	color: #fff;
	font-size: 24px;
	line-height: 1.25;
	margin-bottom: 20px;
}

.text-wrap p {
	color: #fff;
	font-size: 16px;
	line-height: 1.8;
	margin-bottom: 20px;
}

.hero-bg {
	position: relative;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	background-color: #0E1926;
}

.hero-bg .container {
	height: 100%;
}

.video-wrap {
	position: absolute;
	left: 200px;
	width: 1600px;
	height: 100vh;
	z-index: 1;
}

.video-wrap video,
.video-wrap img {
	position: absolute;
	display: block;
	width: 100%;
}

.more-btn {

	display: none;

}
@media screen and (max-width: 768px) {
	.logo {
		top: 3px;
		left : -10px;
		cursor:pointer;
		float: left;
		width: 170px;
		height: auto;
		
		z-index: 20;
	}
	header{
		padding: 0;
		height: 56px;
	}
	header h1{
		height: 56px;
	}
	header h1 img{
		top:20px;
		height: 16px;
	}
	.main-navigation{
		position: fixed;
		top:0;
		right:0;
		padding:50px 20px;
		width: 100%;
		height: 100vh;
		background-color: rgba(14,25,38,0.9);
		opacity: 0;
		visibility: hidden;
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
		transition: 0.3s;
	}
	.main-navigation.active{
		opacity: 1;
		visibility: visible;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
		transition: 0.3s;
	}
	.main-navigation a{
		display: block;
		margin:auto;
		margin-bottom:8px;
		padding:12px;

	}
	.main-navigation a.btn{
		text-align: center;
	}
	.video-wrap{
		top:5vw;
		left:-50%;
		width:200%;
	}
	.text-wrap{
		width:100%;
		height: auto;
		padding:20px;
		top:auto;
		bottom:20px;
	}
	.text-wrap h2{
		font-size: 36px;
	}
	.text-wrap h3{
		font-size: 20px;
	}
	.text-wrap p {
		font-size: 14;
	}

	.more-btn{
		display: block;
		position: absolute;
		right:12px;
		top:8px;
		width:40px;
		height: 40px;
		border-radius: 8px;
		border:none;
		background-color: transparent;
	}
	.more-btn .icon{
		display: block;
		width:24px;
		height: 24px;
		margin: 0 auto;
		background: url(/static/media/menu-icon.bee1874a.svg) no-repeat center;
		background-size: contain;
	}
	.more-btn.active .icon{
		background: url(/static/media/menu-close-icon.511e6f84.svg) no-repeat center;
		background-size: contain;
	}
}

@media screen and (min-width: 769px) {
.logo {
	top: 8px;
    cursor:pointer;
    float: left;
	width: 270px;
	height: auto;
	margin: 0;
	padding: 0;
	z-index: 50;
	}
	

	.main-navigation {
		float: right;
		font-size: 20px;
	}

	.main-navigation a {
		color: #fff;
		margin-left: 30px;
		margin-top: 10px;
	}

	.main-navigation a.active {
		font-weight: bold;
		border-bottom: 2px solid #004fff;
	}
	a {
		font-size: 22px;
		text-decoration: none;
	}
}
